  height: auto;
  display: flex;
  flex-direction: column;
}

@font-face {
  font-family: "Gotham Pro Narrow";
  src: url("../assets/fonts/GothamProNarrow-Medium.woff2") format("woff2"), url("../assets/fonts/GothamProNarrow-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Gotham Pro Narrow";
  src: url("../assets/fonts/GothamProNarrow-Bold.woff2") format("woff2"), url("../assets/fonts/GothamProNarrow-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
*,
*::after,
*::before {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

:root {
  --max-container-width: 150rem;
  --content-padding: 6rem;
  --primary-color: #4659ff;
  --primary-color-hover: rgb(51, 70, 240);
  --text-color: #000;
  --grey-text: #a0a7b2;
  --gotham: "Gotham Pro Narrow", sans-serif;
  --roboto: "Roboto", sans-serif;
  --plyr-color-main: #4659ff;
}
@media only screen and (max-width: 1024px) {
  :root {
    --content-padding: 4rem;
  }
}
@media only screen and (max-width: 640px) {
  :root {
    --content-padding: 2rem;
  }
}

html {
  box-sizing: border-box;
  font-size: 10px;
  -webkit-text-size-adjust: 100%;
  -webkit-tap-highlight-color: transparent;
  font-size: clamp(5px, 0.76vw, 10px);
}
@media only screen and (max-width: 640px) {
  html {
    font-size: 9px;
  }
}
@media only screen and (max-width: 370px) {
  html {
    font-size: 8px;
  }
}

body {
  font-size: 1.6rem;
  line-height: 1.2;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  font-family: sans-serif;
  color: var(--text-color);
  font-family: var(--roboto);
  background-color: #f0f0f0;
}
body.white-bg {
  background-color: #fff;
}

main {
  display: block;
}

ul,
ol {
  list-style: none;
}

ol {
  counter-reset: ordered-list;
}
ol li {
  counter-increment: ordered-list;
}
ol li::before {
  content: counter(ordered-list) ".";
}

input,
textarea {
  border: none;
  border-radius: 0;
  font: inherit;
  outline: none;
  box-shadow: none;
  border-radius: 0;
  width: 100%;
  background-color: transparent;
  display: block;
}

input:not([type=checkbox]):not([type=radio]) {
  appearance: none;
  -webkit-appearance: none;
}

textarea {
  resize: none;
  box-shadow: none;
  display: block;
  width: 100%;
  overflow: auto;
  height: 20rem;
  appearance: none;
  scrollbar-width: none;
}
textarea::-webkit-scrollbar {
  background-color: white;
  width: 0.6rem;
}
textarea::-webkit-scrollbar-thumb {
  background-color: var(--primary-color);
}
textarea::-webkit-scrollbar-button {
  display: none;
}
textarea::-webkit-scrollbar-track {
  background-color: #f5f5f5;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  appearance: none;
  margin: 0;
}

input[type=number] {
  -moz-appearance: textfield;
}

input[type=search] {
  -webkit-appearance: textfield;
  /* 1 */
  outline-offset: -2px;
  /* 2 */
}

input[type=search] {
  -webkit-appearance: textfield;
}

input[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}

input[type=search]::-webkit-search-cancel-button {
  -webkit-appearance: none;
  display: none;
}

button {
  border: none;
  background-color: transparent;
  cursor: pointer;
  user-select: none;
  -webkit-appearance: none;
  font: inherit;
  color: inherit;
  outline: none;
  display: inline-block;
  position: relative;
  transition: color 0.2s;
}

a {
  background-color: transparent;
  text-decoration: none;
  transition: color 0.2s;
}
a:link, a:visited {
  color: inherit;
}

svg {
  flex-shrink: 0;
  fill: currentColor;
  transition: fill 0.2s;
}

img {
  border-style: none;
  max-width: 100%;
  display: block;
}

img[data-sizes=auto] {
  display: block;
  width: 100%;
}

img.lazyload:not([src]) {
  visibility: hidden;
}

table {
  width: 100%;
  border-collapse: collapse;
  text-align: left;
}

th {
  font-weight: 400;
}

td,
th {
  text-align: left;
  vertical-align: top;
}

.lazyload,
.lazyloading {
  opacity: 0;
}

.lazyloaded {
  opacity: 1;
  transition: opacity 300ms;
}

.visually-hidden {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
  white-space: nowrap;
}

.swiper-slide {
  box-sizing: border-box;
  height: auto;
  display: flex;
  flex-direction: column;
}

.no-scroll {
  overflow-y: scroll;
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
}
.no-scroll body {
  height: auto;
  overflow: hidden;
}

::selection {
  color: white;
  background-color: var(--primary-color);
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
textarea:-webkit-autofill,
textarea:-webkit-autofill:hover,
textarea:-webkit-autofill:focus,
select:-webkit-autofill,
select:-webkit-autofill:hover,
select:-webkit-autofill:focus {
  background-color: transparent;
}

.calculator-placeholder {
  margin-bottom: 3.5rem;
}

.calculator-placeholder__image {
  display: block;
  width: 100%;
  height: auto;
  user-select: none;
  pointer-events: none;
}

[class*=ymaps-2][class*=-ground-pane] {
  filter: grayscale(100%);
}

.container {
  width: 100%;
  max-width: calc(var(--max-container-width) + 2 * var(--content-padding));
  margin: 0 auto;
  padding: 0 var(--content-padding);
}
@supports (width: max(20vw, 100px)) {
  .container {
    padding-left: max(env(safe-area-inset-left, 1.4rem), var(--content-padding));
    padding-right: max(env(safe-area-inset-right, 1.4rem), var(--content-padding));
  }
}

.inner-container {
  width: 100%;
  max-width: 112rem;
  margin: 0 auto;
}

.page-content {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.page-main {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
}

.page-header {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  background-color: white;
  z-index: 300;
  color: var(--text-color);
}
.modal-open .page-header {
  padding-right: var(--sb-width, 0);
}
@media only screen and (max-width: 640px) {
  .page-header {
    border-top: 1px solid white;
    border-bottom: 1px solid white;
    position: relative;
  }
}

.page-header__row {
  display: flex;
  min-height: 7.5rem;
}
@media only screen and (max-width: 640px) {
  .page-header__row {
    min-height: auto;
    padding: 1.2rem 0;
    flex-wrap: wrap;
  }
}

.page-header__logo-menu {
  margin-right: 3.2rem;
  flex-shrink: 0;
  display: flex;
  align-items: center;
  width: 22.4rem;
  position: relative;
  align-self: center;
  position: relative;
  justify-content: space-between;
}
@media only screen and (max-width: 640px) {
  .page-header__logo-menu {
    width: 100%;
    margin: 0;
  }
}
.page-header__logo-menu.active .page-header__logo-menu-dropdown {
  opacity: 1;
  visibility: inherit;
  transition: opacity 0.3s, visibility 0.3s;
}

.page-header__logo-menu-dropdown {
  position: absolute;
  top: -1.5rem;
  right: -1.5rem;
  left: -1.5rem;
  padding-top: 5rem;
  background-color: #fff;
  z-index: 20;
  box-shadow: 0 0.4rem 1rem rgba(0, 0, 0, 0.1);
  border-radius: 0.4rem;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s, visibility 0s linear 0.3s;
}

.page-header__logo-menu-dropdown-inner {
  padding: 1.4rem 1.4rem 1.4rem;
}

.page-header__logo-menu-dropdown-link {
  padding: 0.3rem 1rem;
  border-radius: 0.4rem;
  transition: background-color 0.2s, color 0.2s;
  font-weight: 700;
  font-size: 1.4rem;
  line-height: 260%;
  letter-spacing: 0.08em;
  text-transform: uppercase;
  display: block;
}
.page-header__logo-menu-dropdown-link:last-child {
  margin-bottom: 0;
}
.no-touch .page-header__logo-menu-dropdown-link:hover, .touch .page-header__logo-menu-dropdown-link:active {
  background-color: var(--primary-color);
  color: white;
}

.page-header__logo {
  display: block;
  flex-shrink: 0;
  position: relative;
  z-index: 30;
}
@media only screen and (max-width: 640px) {
  .page-header__logo {
    margin: 0 auto;
    order: 2;
  }
}
.page-header__logo svg {
  fill: var(--primary-color);
  display: block;
  height: auto;
  width: 14.6rem;
  height: 2.6rem;
}
@media only screen and (max-width: 640px) {
  .page-header__logo svg {
    width: 14.6rem;
    height: 2.6rem;
  }
}

.page-header__logo-menu-burger {
  flex-shrink: 0;
  margin-left: 2rem;
  display: flex;
  flex-direction: column;
  position: relative;
  z-index: 30;
}
@media only screen and (max-width: 640px) {
  .page-header__logo-menu-burger {
    margin-left: 0;
    margin-right: 2rem;
    order: -1;
  }
}
.page-header__logo-menu-burger span {
  display: block;
  width: 2.4rem;
  background-color: var(--text-color);
  height: 0.3rem;
  margin-bottom: 0.4rem;
  border-radius: 0.2rem;
  transition: opacity 0.2s, transform 0.2s;
}
@media only screen and (max-width: 640px) {
  .page-header__logo-menu-burger span {
    width: 24px;
    height: 3px;
    margin-bottom: 4px;
    border-radius: 2px;
  }
}
.page-header__logo-menu-burger span:last-child {
  margin-bottom: 0;
}
.page-header__logo-menu.active .page-header__logo-menu-burger span:nth-child(1) {
  transform: translateY(0.7rem) rotate(45deg);
}
.page-header__logo-menu.active .page-header__logo-menu-burger span:nth-child(2) {
  opacity: 0;
}
.page-header__logo-menu.active .page-header__logo-menu-burger span:nth-child(3) {
  transform: translateY(-0.7rem) rotate(-45deg);
}
@media only screen and (max-width: 640px) {
  .page-header__logo-menu.active .page-header__logo-menu-burger span:nth-child(1) {
    transform: translateY(7px) rotate(45deg);
  }
  .page-header__logo-menu.active .page-header__logo-menu-burger span:nth-child(2) {
    opacity: 0;
  }
  .page-header__logo-menu.active .page-header__logo-menu-burger span:nth-child(3) {
    transform: translateY(-7px) rotate(-45deg);
  }
}

.page-header__actions {
  align-self: center;
  display: flex;
  align-items: center;
  margin-right: 4rem;
}
@media only screen and (max-width: 640px) {
  .page-header__actions {
    display: none;
  }
}

.page-header__actions-link {
  color: var(--text-color);
  font-size: 1.2rem;
  line-height: 1.6rem;
  display: inline-flex;
  align-items: center;
  margin-right: 3.2rem;
}
.page-header__actions-link:last-child {
  margin-right: 0;
}
.page-header__actions-link:link, .page-header__actions-link:visited {
  color: var(--text-color);
}
.no-touch .page-header__actions-link:hover, .touch .page-header__actions-link:active {
  color: var(--primary-color);
}
.no-touch .page-header__actions-link:hover .page-header__actions-link-icon, .touch .page-header__actions-link:active .page-header__actions-link-icon {
  color: var(--primary-color);
}

.page-header__actions-link-icon {
  width: 2.2rem;
  height: 2.2rem;
  position: relative;
  flex-shrink: 0;
  margin-right: 1.2rem;
  color: transparent;
  transition: color 0.2s;
  margin-top: -0.2rem;
}
.page-header__actions-link-icon svg {
  display: block;
  width: 100%;
  height: 100%;
  fill: var(--primary-color);
}

.page-header__actions-link-notification {
  position: absolute;
  width: 1.8rem;
  height: 1.8rem;
  border-radius: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  font-family: var(--gotham);
  font-weight: bold;
  font-size: 1.2rem;
  line-height: 1.2;
  background-color: var(--primary-color);
  color: white;
  left: 100%;
  margin-top: -0.65rem;
  top: 0;
  margin-left: -1rem;
}

.page-header__user {
  align-self: stretch;
  display: flex;
  align-items: center;
  margin-left: auto;
  margin-right: 4.5rem;
}
@media only screen and (max-width: 640px) {
  .page-header__user {
    display: none;
  }
}

.page-header__username {
  display: flex;
  align-items: center;
  font-weight: 500;
  font-size: 1.6rem;
  line-height: 2.4rem;
}
.page-header__username svg {
  display: block;
  fill: var(--primary-color);
  height: auto;
  width: 2rem;
  height: 2rem;
  margin-left: 1.5rem;
}
.no-touch .page-header__username:hover, .touch .page-header__username:active {
  color: var(--primary-color);
}

.page-header__search {
  flex-shrink: 0;
  width: 22rem;
  align-self: center;
}
@media only screen and (max-width: 640px) {
  .page-header__search {
    display: none;
  }
}

.page-header__search-form {
  display: flex;
  align-items: center;
  background: #f5f5f5;
  border-radius: 0.4rem;
  color: var(--text-color);
  padding: 1rem 1.3rem;
}

.page-header__search-form-input {
  width: 30%;
  flex-grow: 1;
  font-size: 1.6rem;
  line-height: 1.2;
}

.page-header__search-submit {
  flex-shrink: 0;
  width: 1.4rem;
  height: 1.4rem;
  margin-left: 2rem;
  fill: var(--primary-color);
}
.page-header__search-submit svg {
  width: 100%;
  height: 100%;
  display: block;
  fill: var(--primary-color);
}

.page-header__catalog-link {
  font-weight: bold;
  font-size: 1.4rem;
  line-height: 1.6rem;
  letter-spacing: 0.15rem;
  text-transform: uppercase;
  margin-right: 6.4rem;
  align-self: center;
}
@media only screen and (max-width: 640px) {
  .page-header__catalog-link {
    display: none;
  }
}
.no-touch .page-header__catalog-link:hover, .touch .page-header__catalog-link:active {
  color: var(--primary-color);
}

.page-header__mobile-search-link {
  display: none;
}
@media only screen and (max-width: 640px) {
  .page-header__mobile-search-link {
    display: block;
    order: 3;
    margin-left: calc(2rem + 4px);
    position: relative;
    z-index: 300;
  }
  .page-header__mobile-search-link svg {
    display: block;
    width: 20px;
    height: 20px;
    fill: var(--primary-color);
  }
}

.page-header__auth-links {
  display: flex;
  align-items: center;
  margin-right: auto;
}
@media only screen and (max-width: 640px) {
  .page-header__auth-links {
    margin: 0;
    margin-top: 1.5rem;
    width: 100%;
  }
}

.page-header__auth-link {
  font-weight: 500;
  font-size: 1.6rem;
  line-height: 2.4rem;
  padding: 0.8rem 1.6rem;
  font-family: var(--gotham);
  border: 1px solid black;
  transition: background-color 0.2s, border-color 0.2s, color 0.2s;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  color: black;
  flex-shrink: 0;
  white-space: nowrap;
  user-select: none;
  margin-right: 1.8rem;
  border-radius: 0.4rem;
}
@media only screen and (max-width: 640px) {
  .page-header__auth-link {
    flex-grow: 1;
  }
}
.page-header__auth-link:last-child {
  margin-right: 0;
}
.page-header__auth-link:link, .page-header__auth-link:visited {
  color: black;
}
.no-touch .page-header__auth-link:hover, .touch .page-header__auth-link:active {
  background-color: black;
  border-color: black;
  color: white;
}
.page-header__auth-link--blue {
  border-color: var(--primary-color);
  background-color: var(--primary-color);
  color: white;
}
.page-header__auth-link--blue:link, .page-header__auth-link--blue:visited {
  color: white;
}
.no-touch .page-header__auth-link--blue:hover, .touch .page-header__auth-link--blue:active {
  background-color: var(--primary-color-hover);
  border-color: var(--primary-color-hover);
  color: white;
}

.page-footer {
  background-color: #fff;
  color: var(--text-color);
  border-top: 1px solid #e0e6f5;
}

.page-footer__row {
  display: flex;
  align-items: center;
  min-height: 9rem;
}
@media only screen and (max-width: 640px) {
  .page-footer__row {
    min-height: auto;
    flex-direction: column;
    align-items: initial;
    padding: 3rem 0;
  }
}

.page-footer__nav {
  display: flex;
  align-items: center;
  margin-right: 10rem;
}
@media only screen and (max-width: 768px) {
  .page-footer__nav {
    margin-right: 5rem;
    flex-direction: column;
    align-items: initial;
    margin-bottom: 4rem;
    display: grid;
    grid-template-columns: repeat(2, minmax(0, 1fr));
    grid-gap: 2rem;
  }
}

.page-footer__nav-link {
  flex-shrink: 0;
  margin-right: 3.5rem;
  font-size: 1.4rem;
  line-height: 1.6rem;
  font-weight: bold;
  letter-spacing: 0.15rem;
  transition: color 0.2s;
  text-transform: uppercase;
}
@media only screen and (max-width: 640px) {
  .page-footer__nav-link {
    margin: 0;
  }
}
.page-footer__nav-link:last-child {
  margin-right: 0;
}
.no-touch .page-footer__nav-link:hover, .touch .page-footer__nav-link:active {
  color: var(--primary-color);
}

.page-footer__social-list {
  display: flex;
  align-items: center;
  margin-right: 4rem;
  flex-shrink: 0;
}
@media only screen and (max-width: 640px) {
  .page-footer__social-list {
    margin: 0;
    margin-bottom: 4rem;
  }
}

.page-footer__social-list-item {
  margin-right: 2.2rem;
}
.page-footer__social-list-item:last-child {
  margin-right: 0;
}

.page-footer__social-link {
  margin-right: 2.2rem;
  flex-shrink: 0;
}
.page-footer__social-link:last-child {
  margin-right: 0;
}
.page-footer__social-link svg {
  width: 2.2rem;
  height: 2.2rem;
  display: block;
  fill: var(--primary-color);
  transition: fill 0.2s;
}
.no-touch .page-footer__social-link:hover svg, .touch .page-footer__social-link:active svg {
  fill: var(--primary-color-hover);
}

.page-footer__copyright {
  font-size: 1.4rem;
  line-height: 1.4;
  color: #a0a7b2;
  margin-right: 1.3rem;
  margin-left: auto;
}
@media only screen and (max-width: 768px) {
  .page-footer__copyright {
    display: none;
  }
}
@media only screen and (max-width: 640px) {
  .page-footer__copyright {
    display: block;
    margin: 0;
    order: 8;
    margin-bottom: 2rem;
  }
}

.page-footer__policy {
  color: var(--primary-color);
  font-size: 1.4rem;
  line-height: 2.3rem;
  margin-right: 9rem;
}
@media only screen and (max-width: 768px) {
  .page-footer__policy {
    margin-left: auto;
  }
}
@media only screen and (max-width: 640px) {
  .page-footer__policy {
    margin: 0;
    margin-bottom: 2rem;
  }
}
.page-footer__policy:link, .page-footer__policy:visited {
  color: var(--primary-color);
}
.no-touch .page-footer__policy:hover .page-footer__policy-text, .touch .page-footer__policy:active .page-footer__policy-text {
  border-color: transparent;
}

.page-footer__policy-text {
  border-bottom: 1px solid currentColor;
  transition: border-color 0.2s;
}

.page-footer__made-by {
  flex-shrink: 0;
  margin-top: 1rem;
}
@media only screen and (max-width: 640px) {
  .page-footer__made-by {
    margin: 0;
    order: 15;
  }
}
.page-footer__made-by img {
  display: block;
  width: 8.8rem;
  height: auto;
}

.intro {
  position: relative;
  z-index: 1;
  background-color: var(--primary-color);
  color: white;
}

.intro__bg {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
}
.intro__bg::after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
